оящего договора считаются согласованными сторонами. ")]),_c('p',[_c('b',[_vm._v("2. Определения и термины")])]),_c('p',[_vm._v(" 1. В целях настоящей Оферты нижеприведенные термины используются в следующем значении: "),_c('ul',[_c('li',[_vm._v(" — Оферта – предложение заключить настоящий публичный договор на оказание информационных услуг.")]),_c('li',[_vm._v(" — Сайт – интернет-сайт"),_c('a',{attrs:{"href":"#"}},[_vm._v(" https://rossegrn.info")]),_vm._v(", используемый Исполнителем для оказания услуг.")]),_c('li',[_vm._v("— ЕГРН — Единый государственный реестр недвижимости.")]),_c('li',[_vm._v("— Росреестр — Федеральная служба государственной регистрации, кадастра и картографии.")]),_c('li',[_vm._v(" — Информационные услуги — услуги Исполнителя по предоставлению Заказчику информации, содержащейся в Реестрах, в виде официальных электронных документов, выданных Росреестром. ")]),_c('li',[_vm._v(" - Аналитические услуги - услуги Исполнителя по предоставлению Заказчику информации, построенной на основании данных, полученных сервисом из официальных или иных открытых источников. ")]),_c('li',[_vm._v("— Акцепт Оферты — полное и безоговорочное принятие Оферты путем осуществления действий, указанных в п. 9.1. Согласия.")]),_c('li',[_vm._v("— Заказчик — лицо, осуществившее Aкцeпт Оферты и являющееся заказчиком Информационных услуг на условиях заключаемого публичного договора.")]),_c('li',[_vm._v("— Договор на оказание информационных услуг (далее – Договор) – договор между Заказчиком и Исполнителем на предоставление Информационных услуг, который заключается посредством Акцепта Оферты.")])])]),_c('p',[_c('b',[_vm._v("3. Предмет договора")])]),_c('p',[_vm._v(" 1. Предметом настоящего договора является возмездное оказание Заказчику Информационных услуг Исполнителем в соответствии с условиями настоящего договора. ")]),_c('p',[_c('b',[_vm._v("4. Условия оказания услуг")])]),_c('p',[_vm._v(" 1. Исполнитель оказывает заказчику услугу по получению и первичной обработке запрашиваемой 