кументы, подтверждающие право на недвижимость (свидетельство или членская книжка НСТ). Человек наивно полагает, что собственность находится в его законном владении. Только вот в документах госреестра он не укажется как собственник, хотя имущество действительно будет находиться на официальном кадастровом учёте. ")]),_c('p',[_vm._v(" 2. Новые действия с объектом не предпринимались с 31 января 1998 года. ")]),_c('p',[_vm._v(" Так как в базе находятся лишь зарегистрированные после окончания 1998 года данные, то вся нужная информация, касающаяся собственников, до 1998 года запрашивается в Бюро технической инвентаризации, Департаменте городского имущества или в Департаменте жилищной политики и жилищного фонда. ")]),_c('p',[_vm._v(" 3. Выписка на коммунальную квартиру. ")]),_c('p',[_vm._v(" Несмотря на то, что каждому собственнику принадлежит та или иная часть квартиры, она, являясь объектом недвижимости, не сможет содержать записи о владельце. Поэтому производить заказ выписок следует для каждой комнаты отдельно. ")]),_c('p',[_vm._v(" 4. Недавно оформленные права на недвижимость. ")]),_c('p',[_vm._v(" Чтобы база ЕГРН обновилась, требуется время, поэтому произведенные две-три недели назад сделки могут не отображаться. Это происходит из-за того, что местные офисы Росреестра сначала передают данные в Москву. Следовательно, данные и информация появляется в онлайн-базе ЕГРН немного позже. ")]),_c('p',[_vm._v(" 5. Наследство. ")]),_c('p',[_vm._v(" Подразумевается, что в выписке правообладатели не указаны или указаны, но неверно. Произойти это может по многим причинам. Рассмотрим несколько из них. Во-первых, собственник мог законно приватизировать жилплощадь прежде, чем наступил 1998 год. Затем, должной регистрации прав не последовало. Спустя время квартира переходит кому-либо в качестве наследства. Новый владелец тоже не регистрирует собственность. Несмотря на то, что он имеет все основания на получение наследства, в выписке не укажут ни одного владельца. Во-вторых, в необходимом документе может присутствовать исключительно предыдущий владелец. Здесь он смог зарегистрировать имущество в Росреестре, но вот его наследник этого не сделал. В такой ситуации в нужной выписке отобразится только предыдущий владелец. ")]),_c('p',[_vm._v(" 6. По одному адресу существует не один номер кадастра. ")]),_c('p',[_vm._v(" Несостыковка в базе реестра могла возникнуть, как во время разделения объекта, так и из-за сложного адреса, который был указан неправильно. ")]),_c('p',[_vm._v(" 7. Ошибка сайта или неточность номера кадастра. ")]),_c('p',[_vm._v(" Иногда владелец оказывается невиновным в отсутствии верных и актуальных сведений в реестре Ошибки происходят из-за проведенных кадастровых работ или при передаче информации из БТИ. Даже в самом Росреестре возникают сбои. Информация, которая должна поступить в реестр недвижимости из этого портала, может оказаться неверной. При обнаружении несоответствий необходимо написать обращение в Росреестр или МФЦ. После обращения будут исправлены и технические, и кадастровые ошибки. ")])])]),_c('div',{staticClass:"questions__item"},[_c('div',{staticClass:"questions__item-title"},[_vm._v(" Как убедиться в подлинности и наличии ЭЦП? "),_c('div',{staticClass:"questions__icon-plus"})]),_c('div',{staticClass:"questions__item-content"},[_c('p',{staticClass:"questions__text--mb"},[_vm._v(" Согласно нормативным актам проверка любых ЭЦП должна осуществляться в удостоверяющих центрах, прошедших аккредитацию. К ним относится сайт Госуслуг и некоторые другие. ")]),_c('p',[_c('b',[_vm._v("Как осуществить проверку:")])]),_c('div',{staticClass:"questions__step"},[_c('p',[_vm._v(" 1. С почты или сайта (раздел "),_c('b',[_vm._v("«Мои заказы»")]),_vm._v(" ) скачиваем архив, где находится выписка. ")]),_c('a',{staticClass:"fancybox",attrs:{"href":"images/questions/screen2.jpg","data-fancybox":""}},[_c('img',{attrs:{"src":"images/questions/screen2.jpg","alt":""}})])]),_c('div',{staticClass:"questions__step"},[_c('p',[_vm._v(" 2. Распаковывае